.App {
  width: 90%;
  margin: 2em auto;
  max-width: 350px;
  /* border: 1px solid magenta; */
}

.order-summary-card {
  border-radius: 20px;
  width: 80%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  height: 100%;
  transition: .3s all ease-in-out;
  /* border: 1px solid magenta; */
}

.order-summary-card:hover {
  box-shadow: 0 10px 15px var(--Desaturatedblue);
}

.order-summary-img {
  width: 100%;
}

.order-summary-img img{
  width: 100%;
  height: 150px;
  object-fit: cover;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
}

.order-summary-details {
  padding: 1em;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1em;
  margin-bottom: 1em;
}

.order-summary-header {
  font-size: 1.2rem;
  font-weight: 900;
  color: var(--Darkblue);
}

.order-summary-desc {
  padding: 0 1em;
  font-weight: 500;
  font-size: .875rem;
  color: var(--Desaturatedblue);
  line-height: 1.5;
}

.order-summary-subscription {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 95%;
  background-color: var(--Verypaleblue);
  padding: .8em .3em;
  border-radius: 10px;
  transition: .3s all ease-in-out;
}

.order-summary-subscription:hover {
  box-shadow: 0 10px 15px var(--Desaturatedblue);
}

.order-summary-plan {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: .8em;
}

.order-summary-subscription-plan {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  color: var(--Darkblue);
  font-weight: 900;
  margin: 0;
}

.order-summary-subscription-plan p:last-child {
  color: var(--Desaturatedblue);
  font-weight: 700;
}

.ri-music-2-fill {
  color: var(--Desaturatedblue);
  background-color: var(--Paleblue);
  padding: .3em;
  border-radius: 100%;
  font-size: 1.5rem;
}

a {
  color: var(--Brightblue);
  font-weight: 700;
  font-size: .875rem;
  transition: .3s all ease-in-out;
}

a:hover {
  text-decoration: none;
}

.order-summary-btn {
  border: 0;
  padding: 1em 2em;
  width: 95%;
  border-radius: 10px;
  font-weight: 700;
  cursor: pointer;
}

.Proceed {
  background-color: var(--Brightblue);
  color: #fff;
  letter-spacing: .02em;
  transition: .3s all ease-in-out;
}

.Proceed:hover {
  box-shadow: 0 10px 10px var(--Desaturatedblue);
}

.cancel {
  background-color: transparent;
  padding: 0;
  color: var(--Desaturatedblue);
  font-weight: 900;
  transition: .3s all ease-in-out;
}

.cancel:hover {
  color: var(--Darkblue);
}
